<template>
  <div class="container">
    <h2>Buscar paciente</h2>
    <form>
      <input type="text" id="dni-input" placeholder="Ingrese DNI" class="btn"/>
      <button v-on:click="filterPatients" class="btn btn-primary">Buscar</button>
    </form>

    <div v-if="foundPatient != '' && foundPatient != null" class="d-flex justify-content-center mt-4">
      <Patientbox :paciente="foundPatient[0]"/>
    </div>

    <div v-else-if="foundPatient != null" class="mt-4">
      <div class="btn border border-danger text-danger">No se encontro ningun paciente</div>
      <router-link to="/addpaciente">
        <div class="btn btn-primary mt-2">Agregar paciente</div>
      </router-link>
      
    </div>
  </div>
</template>

<script>
import Navbar from '@/components/Navbar.vue';
import Patientbox from '@/components/Patientbox.vue';

export default {
  name: "Patientsearch",
  components: {
    Navbar,Patientbox
  },
  data() {
    return {
      patients: [],
      foundPatient: null
    };
  },
  computed: {
    url(){
      return '/paciente/' + this.foundPatient[0].id;
    }
    },
  methods: {
    async fetchData() {
      await fetch("https://raw.githubusercontent.com/21diego/database/master/pacientes.json")
        .then(response => response.json())
        .then(json => (this.patients = json));
    },

    filterPatients(e) {
      e.preventDefault();
      this.foundPatient = this.patients.filter(
        e => document.getElementById("dni-input").value == e.documento
      );
    }
  },
  created: function() {
    this.fetchData();
  }
}
</script>

<style scoped>
#backbtn{
  position: fixed;
  top: 20px;
  right: 1em;
}
#dni-input{
  width: 40vw;
  background-color: rgb(226, 255, 255);
  border: #007bff solid 1px;
  margin-right: 1em;
}
</style>
